import type { Metadata } from "next"
import { SignIn,ClerkProvider } from "@clerk/nextjs"

export const metadata: Metadata = {
  title: "Login | Standing Desk Admin",
  description: "Login to the standing desk admin dashboard",
}

export default function LoginPage() {
  return (
    <ClerkProvider>
    <div className="flex min-h-screen items-center justify-center bg-gray-50 px-4 py-12 sm:px-6 lg:px-8">
      <div className="w-full max-w-md space-y-8">
        <div className="text-center">
          <h1 className="text-3xl font-bold tracking-tight text-gray-900">Admin Login</h1>
          <p className="mt-2 text-sm text-gray-600">Sign in to access the standing desk admin dashboard</p>
        </div>
        <div className="mt-8">
          <SignIn
            appearance={{
              elements: {
                formButtonPrimary: "bg-emerald-600 hover:bg-emerald-700",
                footerActionLink: "text-emerald-600 hover:text-emerald-500",
              },
            }}
            redirectUrl="/admin/dashboard"
          />
        </div>
      </div>
    </div>
    </ClerkProvider>
  )
}